<template>
  <div id="result">
    {{ num }}
  </div>
</template>
<script>
import result from '@/result.json'
export default {
  data () {
    return {
      num: parseInt(this.$route.params.num),
      result: result
    }
  },
  created () {
    console.log()
    // let img = require(`@/assets/image/${this.result[this.num].image}}`)
    let tagList = [
      {
        attr: 'og:url',
        value: 'https://pigchallenge.laiphaute.com'
      },
      {
        attr: 'og:type',
        value: 'website'
      },
      {
        attr: 'og:title',
        value: '新春大挑戰！你是什麼豬？'
      },
      {
        attr: 'og:description',
        value: `你答對了${this.num}題`
      },
      {
        attr: 'og:image',
        value: `/share-img/${this.result[this.num].image}`
      }
    ]
    let theTagList = tagList.map(tag => {
      const tagEle = document.createElement('meta')
      tagEle.setAttribute(tag.attr, tag.value)
      tagEle.setAttribute('data-vue-router-controlled', '')
      if (document.querySelector(`[property="${tag.attr}"]`)) {
        document.querySelector(`[property="${tag.attr}"]`).remove()
      }
      return tagEle
    }).forEach(tag => {
      // console.log(tag.attr)
      // console.log(document.querySelector(`[property="${tag.attr}"]`))
      // document.head.removeChild(tag.attr)
      document.head.appendChild(tag)
    })

  }
}
</script>
